ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  font-family: Noto Sans JP, sans-serif;
}

section {
  min-height: 100vh;
  background: url("pexels-pixabay-237464.8a5782ff.jpg") center / cover no-repeat;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  display: flex;
}

.key__list {
  flex-wrap: wrap;
  display: flex;
}

.key__item {
  width: 110px;
  text-align: center;
  color: #faebd7;
  text-shadow: 0 0 5px #000;
  background: rgba(0, 0, 0, .5);
  border: 1px solid #7cfc00;
  border-radius: 5px;
  margin: 1rem;
  padding: 1rem .5rem;
  transition: all 70ms;
}

.playing {
  text-shadow: 0 0 10px #7fff00;
  border-color: #7fff00;
  transform: scale(1.1);
}

kbd {
  font-family: inherit;
  font-size: 40px;
  display: block;
}

.key__sound {
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #7fff00;
  font-size: 1.2rem;
}

.back-link {
  color: #5f9ea0;
  text-decoration: none;
  position: absolute;
  top: 20px;
  left: 20px;
}

